import { Component, Inject, OnInit, TemplateRef, ViewChild } from '@angular/core';
import icClose from '@iconify/icons-ic/twotone-close';
import icFilterList from '@iconify/icons-ic/twotone-filter-list';
import icFileExcel from "@iconify/icons-fa-solid/file-excel";
import icSearch from "@iconify/icons-ic/twotone-search";
import icDelete from '@iconify/icons-ic/twotone-delete';
import icAdd from '@iconify/icons-ic/twotone-add';
import { DocumentType } from 'src/app/shared/enum/document-type.enum';
import { FormControl } from '@angular/forms';
import { GridComponent } from 'src/app/shared/component/grid/grid.component';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material';
import { AuthService } from 'src/app/core/services/auth.service';
import { NotificationService } from 'src/app/core/services/notification.service';
import { ApiService } from 'src/app/core/services/api.service';
import { LoadingComponent } from 'src/app/shared/component/dialogs/loading/loading.component';
import { SecurityOperation } from 'src/app/shared/enum/security-operation.enum';
import { ConfirmComponent } from 'src/app/shared/component/dialogs/confirm/confirm.component';
import { SystemMessage } from 'src/app/shared/configdata/system-message';
import { CommonService } from 'src/app/core/utilities/common.helper';
import { ApiError } from 'src/app/shared/models/api-error.model';
import icDone from '@iconify/icons-ic/twotone-done';
import { LoaiGoiDichVu } from '../goi-dv-marketing.model';

@Component({
  selector: 'app-loai-dich-vu-popup',
  templateUrl: './loai-dich-vu-popup.component.html',
  styleUrls: ['./loai-dich-vu-popup.component.scss']
})
export class LoaiDichVuPopupComponent implements OnInit {
  loaiGoiDichVu: LoaiGoiDichVu = new LoaiGoiDichVu();
  isCreate = false;
  icDone = icDone;
  icClose = icClose;
  icSearch = icSearch;
  icFileExcel = icFileExcel;
  icFilterList = icFilterList;
  icDelete = icDelete;
  icAdd = icAdd;
  documentType: DocumentType = DocumentType.DanhMucNoiGioiThieu;
  searchString: string = null;
  searchCtrl = new FormControl();
  validationErrors: any;
  popupLoadingData: any;
  popupDeletingData: any;
  isShow: boolean = false;
  gridColumns: any[] = [];
  _gridColumnsFilter: any[] = [];
  _isCheckColumnFilter: boolean = true;
  urlGetData = "GoiDichVuChuongTrinhMarketing/GetDataForGridLoaiGoiMarketingAsync";
  urlGetTotalPage = "GoiDichVuChuongTrinhMarketing/GetTotalPageForGridLoaiGoiMarketingAsync";
  @ViewChild('maTemplate', { static: true }) maTemplate: TemplateRef<any>;
  @ViewChild('actionTemplate', { static: true }) actionTemplate: TemplateRef<any>;
  @ViewChild('trangThaiTemplate', { static: true }) trangThaiTemplate: TemplateRef<any>;

  @ViewChild("grid", { static: true }) grid: GridComponent;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private authService: AuthService,
    private notificationService: NotificationService,
    private apiService: ApiService,
    private dialog: MatDialog
  ) { }

  ngOnInit() {
    this.gridColumns = [
      { Field: "Ma", Title: "M", Width: 80, Sortable: true, Template: this.maTemplate },
      { Field: "Ten", Title: "Tn", Width: 260, Sortable: true },
      // { Field: "IsDefault", Title: "Trng Thi", Width: 200, Template: this.trangThaiTemplate },
      { Field: "Action", Title: "", Width: 40, Sortable: false, Template: this.actionTemplate }
    ];
    this._gridColumnsFilter = this.gridColumns.filter(p => p.Title != '');

  }
  onKey(event: any) {
    if (event.key == "Enter") {
      this.grid.searchString = this.searchString;
      this.grid.search();
    }
  }

  searchChange(event: any) {
    if (event == null || event == "") {
      this.clearSearch();
    }
  }

  clearSearch() {
    this.searchString = null;
    this.grid.searchString = null;
    this.grid.search();
  }

  popupSavingData: any = null;
  savingPage() {
    this.popupSavingData = this.dialog.open(LoadingComponent, {
      disableClose: true,
      width: "200px",
      height: "90px",
      data: { Title: "ang lu d liu..." },
    });
  }

  closepopupSavingData() {
    if (this.popupSavingData != undefined && this.popupSavingData != null) {
      this.popupSavingData.close();
    }
  }

  popupAddingData: any = null;
  addingPage() {
    this.popupAddingData = this.dialog.open(LoadingComponent, {
      disableClose: true,
      width: "200px",
      height: "90px",
      data: { Title: "ang thm d liu..." },
    });
  }

  closePopupAddingData() {
    if (this.popupAddingData != undefined && this.popupAddingData != null) {
      this.popupAddingData.close();
    }
  }

  loadingPage() {
    this.popupLoadingData = this.dialog.open(LoadingComponent, {
      disableClose: true,
      width: "200px",
      height: "90px",
      data: { Title: "ang ti d liu..." },
    });
  }

  closePopupLoadingData() {
    if (this.popupLoadingData != undefined && this.popupLoadingData != null) {
      this.popupLoadingData.close();
    }
  }

  deletingPage() {
    this.popupDeletingData = this.dialog.open(LoadingComponent, {
      disableClose: true,
      width: "200px",
      height: "90px",
      data: { Title: "ang xa d liu..." },
    });
  }

  closePopupDeletingData() {
    if (this.popupDeletingData != undefined && this.popupDeletingData != null) {
      this.popupDeletingData.close();
    }
  }

  them() {
    this.validationErrors = [];
    this.loaiGoiDichVu = new LoaiGoiDichVu();
    this.isShow = true;
    this.isCreate = true;
  }
  isDefault: boolean = false;
  luu() {
    if (this.isCreate) {
      if (this.authService.hasPermission(this.documentType, SecurityOperation.Add)) {
        this.addingPage();
        this.apiService.post<any>("GoiDichVuChuongTrinhMarketing/ThemLoaiGoi", this.loaiGoiDichVu).subscribe(
          () => {
            this.closePopupAddingData();
            let mess = CommonService.format(SystemMessage.ActionSuccessfully, ["Thm"]);
            this.notificationService.showSuccess(mess);
            this.grid.search();
            this.huy();
          },
          (err: ApiError) => {
            this.validationErrors = err.ValidationErrors;
            if (err.Message != "Validation Failed") {
              this.notificationService.showError(err.Message);
            }
            this.closePopupAddingData();
          });
      }
      else {
        this.notificationService.showError(SystemMessage.UnAuthorizedMessage);
      }
    } else {
      if (this.authService.hasPermission(this.documentType, SecurityOperation.Update)) {
        if (this.isDefault) {
          this.notificationService.showError("D liu ny khng th sa (d liu mc nh).");
        } else {
          this.savingPage();
          this.apiService.post<any>("GoiDichVuChuongTrinhMarketing/CapNhatLoaiGoi", this.loaiGoiDichVu).subscribe(
            () => {
              this.closepopupSavingData();
              let mess = CommonService.format(SystemMessage.ActionSuccessfully, ["Lu"]);
              this.notificationService.showSuccess(mess);
              this.grid.search();
              this.huy();
            },
            (err: ApiError) => {
              this.validationErrors = err.ValidationErrors;
              if (err.Message != "Validation Failed") {
                this.notificationService.showError(err.Message);
              }
              this.closepopupSavingData();
            });
        }
      }
      else {
        this.notificationService.showError(SystemMessage.UnAuthorizedMessage);
      }
    }
  }

  huy() {
    this.validationErrors = [];
    this.loaiGoiDichVu = new LoaiGoiDichVu();
    this.isShow = false;
    this.isCreate = false;
  }

  xem(id: number, isDefault: boolean) {
    this.isDefault = isDefault;
    if (this.authService.hasPermission(this.documentType, SecurityOperation.Update)) {
      this.loadingPage();
      this.apiService.get<any>("GoiDichVuChuongTrinhMarketing/GetLoaiGoi?id=" + id)
        .subscribe((resultData) => {
          this.loaiGoiDichVu = resultData;
          this.isShow = true;
          this.closePopupLoadingData();
        },
          (err: ApiError) => {
            if (err.Message != "Validation Failed") {
              this.notificationService.showError(err.Message);
            }
            this.closePopupLoadingData();
          }
        );
    }
    else {
      this.notificationService.showError(SystemMessage.UnAuthorizedMessage);
    }
  }

  xoa(id: number, isDefault: boolean) {
    if (isDefault) {
      this.notificationService.showError("D liu ny khng th xa (d liu mc nh).");
    } else {

      this.dialog.open(ConfirmComponent, {
        disableClose: false,
        width: '400px',
        data: { Title: "Xc nhn", Message: CommonService.format(SystemMessage.MessConfirm, ["xa"]) }
      }).afterClosed().subscribe(result => {
        if (result === 'Yes') {
          if (this.authService.hasPermission(this.documentType, SecurityOperation.Delete)) {
            this.deletingPage();
            this.apiService.post<any>("GoiDichVuChuongTrinhMarketing/XoaLoaiGoi?id=" + id)
              .subscribe(() => {
                this.notificationService.showSuccess(CommonService.format(SystemMessage.ActionSuccessfully, ["Xa"]));
                this.huy();
                this.grid.search();
                this.closePopupDeletingData();
              },
                (err: ApiError) => {
                  this.validationErrors = err.ValidationErrors;
                  if (err.Message != "Validation Failed") {
                    this.notificationService.showError(err.Message);
                  }
                  this.closePopupDeletingData();
                }
              );
          }
          else {
            this.notificationService.showError(SystemMessage.UnAuthorizedMessage);
          }
        }
      });
    }
  }

}
